<template>
<div>
	<div class="row">
		<div class="col-lg-12 col-md-12">
			<div class="card">
				<div class="card-content">
					<div class="row-flex">
						Наша реферальная программа награждает вас деньгами за каждого приглашенного  реферала							
					</div>
					<div class="row-flex m-t-15">
						<div class="row nl">
							<button v-if="referalLink === null" type="button" class="btn btn-success" v-on:click='onGetReferralLink()'>
								<small>Получить реферальную ссылку</small>
							</button>
							<div v-else>
								Одноразовый реферальный код: <strong>{{url}}#/register/{{ referalLink }}</strong>
							</div>
						</div>
					</div>
				</div>
			</div>
			<div class="card">
				<div class="card-header">
					<h4 class="card-title">Список ваших рефералов</h4>
				</div>
				<div class="card-content row">
					<div class="col-sm-12">
					<el-table :data="tableData">
						<el-table-column type="index"></el-table-column>
						<el-table-column label="Time">
							<template slot-scope="props">
								{{ props.row.puttime | moment('DD MMM YYYY, HH:mm') }}
							</template>
						</el-table-column>
						<el-table-column prop="email" label="Email"></el-table-column>
						<el-table-column label="Status">
							<template slot-scope="props">
								<template v-if="props.row.status == 1">
									Активирован
								</template>
								<template v-else-if="props.row.status == 0">
									Ожидает подтверждения
								</template>
								<template v-else>
									Неизвесный статус
								</template>
							</template>
						</el-table-column>
						<!-- <el-table-column label="Actions">
							<template slot-scope="props">
								<a class="btn btn-simple btn-info btn-xs btn-icon like" @click="handleLike(props.$index, props.row)"><i class="ti-heart"></i></a>
								<a class="btn btn-simple btn-warning btn-xs btn-icon edit" @click="handleEdit(props.$index, props.row)"><i class="ti-pencil-alt"></i></a>
								<a class="btn btn-simple btn-danger btn-xs btn-icon remove"  @click="handleDelete(props.$index, props.row)"><i class="ti-close"></i></a> 
							</template>
						</el-table-column> -->
					</el-table>
					</div>
				</div>
			</div>
		</div>
	</div>
</div>
</template>
<script>
	import Vue from 'vue'
	import swal from 'sweetalert2'
	import {Table, TableColumn} from 'element-ui'
	import PSwitch from 'src/components/UIComponents/Switch.vue'

	Vue.use(Table)
	Vue.use(TableColumn)
	// import api from '@/api'
	export default {
		components: {
			PSwitch
		},
		data () {
			return {
				referalLink: null,
				tableData: []
			}
		},
		computed: {
			url () {
				var getUrl = window.location
				var baseUrl = getUrl.protocol + '//' + getUrl.host + '/' + getUrl.pathname.split('/')[1]
				return baseUrl
			}
		},
		methods: {
			debug (val) {
				console.log(val)
			},
			onGetReferralLink () {
				swal({
					position: 'top-right',
					title: 'Получаем данные...',
					showConfirmButton: false,
					onOpen: () => {
					swal.showLoading()
					}
				}).catch(swal.noop)
				var self = this
				this.$store.dispatch('referralGetNewLink', {
					data: {},
					callback: function (data) {
						self.referalLink = data.item
						swal.close()
					},
					successFalse: function (data) {
						var error = (data.message === 'Failed to fetch')
							? 'Невозможно сделать запрос, нет доступа к серверу или интернету' : data.message
						swal({
							title: 'Ошибка',
							text: error,
							type: 'error',
							showConfirmButton: true
						})
					}
				})
			},
			onGetLinkItems () {
				swal({
					position: 'top-right',
					title: 'Получаем данные...',
					showConfirmButton: false,
					onOpen: () => {
					swal.showLoading()
					}
				}).catch(swal.noop)
				var self = this
				this.$store.dispatch('referralGetLinkItems', {
					data: {},
					callback: function (data) {
						self.tableData = data.items
						swal.close()
					},
					successFalse: function (data) {
						var error = (data.message === 'Failed to fetch')
							? 'Невозможно сделать запрос, нет доступа к серверу или интернету' : data.message
						swal({
							title: 'Ошибка',
							text: error,
							type: 'error',
							showConfirmButton: true
						})
					}
				})
			}
		},
		watch: {
		},
		created () {
		},
		mounted () {
			this.onGetLinkItems()
		}
	}
</script>
<style>
.card .row.nl {
	margin-right: 0px;
	margin-left: 0px;
}
</style>



